import React, { useState, useEffect, useRef } from "react";
import { cx, css, keyframes } from "@emotion/css";
import patterns from "./patterns";
import { GiAlienSkull as EntityIcon } from "react-icons/gi";

/**
 * settings
 */
const lifeSpeed = 1000 / 2; // in ms
const side = 2; // number of patterns by size

/**
 * Unlock value
 */
const patternWidth = 8; // based on patterns preset

/**
 * world styles
 */
const entityItem = css`
  position: relative;
  width: calc(100% / (${side} * ${patternWidth}));
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
  }
  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
`;
const entityStyles = (row, cell) => {
  const oddRow = !!(row % 2);
  const oddCell = !!(cell % 2);

  if (oddRow && oddCell)
    return css`
      opacity: 0.75;
      transform: scale(0.8);
    `;
  if (!oddRow && oddCell)
    return css`
      opacity: 1;
      transform: rotate(-2deg);
    `;
  if (oddRow && !oddCell)
    return css`
      opacity: 0.8;
      transform: scale(1.15) rotate(-1deg);
    `;
  return css`
    opacity: 0.85;
    transform: scale(0.9) rotate(2.5deg);
  `;
};

/**
 * Create world life according patterns
 * @param {number} sideLength
 * @returns {array}
 * @description compose a map by juxtaposing 8x8 patterns
 * Only square are generated so a sideLength of 2 will mix 2x2 patterns,
 * so 4 patterns for a 16x16 world
 */
function createWorldFromPattern(side = 2) {
  const selection = Array();
  const getRandomPattern = () =>
    patterns[Math.floor(Math.random() * patterns.length)];

  if (side > 1) {
    for (var i = 0; i < side * side; i++) {
      if (!!!(i % side)) {
        selection.push(getRandomPattern());
      } else {
        const last = selection.length - 1;
        const newPattern = getRandomPattern();
        selection[last] = selection[last].map((line, index) =>
          line.concat(newPattern[index])
        );
      }
    }
  } else {
    selection.push(getRandomPattern());
  }

  return {
    map: selection.flat(),
    stillAlive: true,
    stillEvolute: true,
  };
}

/**
 * Count neighbors around an entity
 * @param {array} world map
 * @param {number} index of the row
 * @param {number} index of the col
 * @returns {number}
 */
function checkNeighbors(map, row, col) {
  let aliveNeighbors = 0;
  let index = 0;
  const nh = [
    { row: row - 1, col: col },
    { row: row - 1, col: col - 1 },
    { row: row - 1, col: col + 1 },
    { row: row, col: col - 1 },
    { row: row, col: col + 1 },
    { row: row + 1, col: col },
    { row: row + 1, col: col - 1 },
    { row: row + 1, col: col + 1 },
  ];
  while (index < 8 && aliveNeighbors < 4) {
    if (!!map[nh[index].row] && !!map[nh[index].row][nh[index].col]) {
      aliveNeighbors += 1;
    }
    index += 1;
  }
  return aliveNeighbors;
}

/**
 * Handle the life evolution
 * @param {object} world object
 * @returns {object} new world object
 */
function lifeEvolution({ map }) {
  let stillAlive = false;
  let stillEvolute = false;
  let storeScore = [];

  // This is important to analyze the map before to transform it
  for (var ri in map) {
    storeScore[ri] = Array();
    for (var ci in map[ri]) {
      storeScore[ri][ci] = checkNeighbors(map, parseInt(ri), parseInt(ci));
    }
  }

  // and so after we handle the evolution according the analyze
  for (var ri in storeScore) {
    for (var ci in storeScore[ri]) {
      const score = storeScore[ri][ci];

      // Any dead cell with three live neighbours becomes a live cell
      if (!map[ri][ci] && score === 3) {
        stillAlive = true;
        stillEvolute = true;
        map[ri][ci] = 1;
      }

      // Any live cell with two or three live neighbours survives
      else if (map[ri][ci] && (score === 2 || score === 3)) {
        stillAlive = true;
        map[ri][ci] = 1;
      }

      // All other live cells die in the next generation.
      else if (map[ri][ci]) {
        stillEvolute = true;
        map[ri][ci] = 0;
      }

      // Similarly, all other dead cells stay dead.
      else {
        map[ri][ci] = 0;
      }
    }
  }

  return {
    map,
    stillAlive,
    stillEvolute,
  };
}

/**
 * ////////////////////////////////
 * The game of life by John Conway
 * ////////////////////////////////
 */
function GameLife() {
  const lifeTime = useRef(null);
  const [world, setWorld] = useState(createWorldFromPattern(side));
  const [generationCount, setGenenationCount] = useState(1);
  const isApocalypse = useRef(false);

  // handle world evolution
  const handleWorld = () => {
    if (isApocalypse.current) return;

    setTimeout(() => {
      setWorld(lifeEvolution);
      setGenenationCount((prev) => prev + 1);
      requestAnimationFrame(handleWorld);
    }, lifeSpeed);
  };

  // Stop the animation when nothing else will happens
  useEffect(() => {
    if (world.stillAlive && world.stillEvolute && !isApocalypse.current) return;
    isApocalypse.current = true;
    cancelAnimationFrame(lifeTime.current);
  }, [world]);

  // init and unmount
  useEffect(() => {
    lifeTime.current = requestAnimationFrame(handleWorld);
    return () => cancelAnimationFrame(lifeTime.current);
  }, []);

  // prevent bad render
  if (!world?.map) return <></>;

  // render
  return (
    <div className="w-100">
      {world["map"].map((row, rindex) => (
        <div className="d-flex flex-row flex-nowrap w-100" key={rindex}>
          {row.map((value, cindex) => (
            <div className={entityItem} key={cindex}>
              {!!value && (
                <EntityIcon className={entityStyles(rindex, cindex)} />
              )}
            </div>
          ))}
        </div>
      ))}
    </div>
  );
}

export default GameLife;
